<template>
  <div class="todo-wrapper">
    <el-popover placement="bottom-start" :width="250"
                trigger="click" ref="outerPopover">
      <template #reference><i class="el-icon-arrow-down"/></template>
      <div v-for="(todo,index) of todos" :key="todo.id">
        <el-checkbox :checked="todo.completed" @change="todoStatusChange(index)">{{ todo.content }}</el-checkbox>
        <p class="todo-item-right">
          |<el-popover
            placement="right"
            trigger="click"
            :ref="'popover-'+index"
            @show="showTodoEdit(index)">
            <i class="el-icon-edit" slot="reference"/>
            <el-input v-model="editTodo" @keyup.enter.native="confirmChangeTodo(index)"/>
          </el-popover>
          <i class="el-icon-circle-close" @click="deleteTodo(index)"/>
        </p>
      </div>
      <el-divider class="todo-divider"/>
      <div>
        {{
          todos.filter(todo => {
            return !todo.completed
          }).length
        }}项待办
      </div>
    </el-popover>

    <el-input v-model="newTodo"
              placeholder="回车添加待办项"
              class="newTodoInput"
              @keyup.enter.native="addTodo"></el-input>
  </div>
</template>

<script>
import {
  requestAddTodo,
  requestChangeTodoStatus,
  requestDeleteTodo,
  requestEditTodo,
  requestGetTodos
} from "@/network/api";
import {formatDateToTimestamps} from "@/utils/DateUtil";

export default {
  name: "Todo",
  filters: {
    all: todos => {
      return todos
    },
    active: todos => {
      return todos.filter(todo => {
        return !todo.completed;
      })
    },
    completed: todos => {
      return todos.filter(todo => {
        return todo.completed
      })
    }
  },
  created() {
    let time=new Date(formatDateToTimestamps(new Date())).getTime()
    requestGetTodos(time).then(res => {
      if (res.data.code === "00000") {
        for (let todo of res.data.data) {
          this.todos.push(new Object({
            'id': todo.id,
            'content': todo.content,
            'completed': todo.status == 1 ? true : false
          }))
        }
      }
    })
  },
  data() {
    return {
      icon: 'el-icon-check',
      newTodo: '',
      editTodo: '',
      todos: [
        // {id: 1, content: "计网第一章复习", completed: false},
        // {id: 2, content: "人工智能考试复习", completed: true},
        // {id: 3, content: "web实验报告和录屏", completed: false},
        // {id: 4, content: "计设签到图ui", completed: true}
      ],
    }
  },
  methods: {
    addTodo() {
      //发送网络请求添加todo
      let todoFormData=new FormData()
      todoFormData.append("content",this.newTodo)
      requestAddTodo(todoFormData).then(res => {
        if (res.data.code === "00000") {
          this.todos.push(new Object({
            'id': res.data.data.id,
            'content': this.newTodo,
            'completed': false
          }))
          this.newTodo=''
      this.$refs.outerPopover.doShow()
        }
      })

      //模拟添加成功
      // this.todos.push(new Object({
      //   'id': this.todos[this.todos.length - 1].id + 1,
      //   'content': this.newTodo,
      //   'completed': false
      // }))
      // this.newTodo = ''
      // this.$refs.outerPopover.doShow()
    },

    confirmChangeTodo(index) {
      // console.log(this.todos[index].id)
      // console.log("即将修改的todo" + this.editTodo)

      //发送网络请求修改todo内容
      let todoFormData = new FormData()
      todoFormData.append("id", this.todos[index].id)
      todoFormData.append("content", this.editTodo)
      requestEditTodo(todoFormData).then(res => {
        if (res.data.code === "00000") {
          this.todos[index].content = this.editTodo
          this.$refs['popover-'+index][0].doClose()
        }
      })

      //编辑todo成功时
      // this.todos[index].content = this.editTodo
      // this.$refs['popover-' + index][0].doClose()
      // console.log(this.todos)
    },

    showTodoEdit(index) {
      this.editTodo = this.todos[index].content
    },

    deleteTodo(index) {
      // 发送网络请求改变状态
      let todoFormData = new FormData()
      todoFormData.append("id", this.todos[index].id)
      requestDeleteTodo(todoFormData).then(res => {
        if (res.data.code === "00000") {
          this.todos.splice(index, 1)
        }
      })

      //模拟删除todo成功
      // this.todos.splice(index, 1)
      // console.log("删除后")
      // console.log(this.todos)
    },
    todoStatusChange(index) {
      //发送网络请求改变状态
      let todoFormData = new FormData()
      todoFormData.append("id", this.todos[index].id)
      requestChangeTodoStatus(todoFormData).then(res => {
        if (res.data.code === "00000") {
          this.todos[index].completed = !this.todos[index].completed
        }
      })

      //模拟改变状态成功
      // this.todos[index].completed = !this.todos[index].completed
      // console.log("改变状态后")
      // console.log(this.todos)
    }
  }
}
</script>

<style scoped>
.todo-wrapper {
  width: 263px;
  display: flex;
}

.el-icon-arrow-down {
  transform: scale(2.5);
  margin-top: 14px;
}

.newTodoInput {
  width: 230px;
  display: inline;
  margin-left: 17px;
}


.todo-item-right {
  width: 50px;
  float: right;
}

.todo-item-right:hover i {
  display: inline;
  margin-left: 8px;
}

.el-icon-edit {
  color: #2189EA;
  transform: scale(1.3);
  display: none;
}

.el-icon-circle-close {
  color: red;
  transform: scale(1.3);
  display: none;
}

.todo-divider {
  margin-top: 6px;
  margin-bottom: 8px;
}
</style>
