<template>
  <div class="home-wrapper">
    <Todo class="todo"/>

    <div class="home-left">
      <div class="date-wrapper">
        <span>{{ date }}</span>
        <el-date-picker
          class="date-picker"
          size="mini"
          v-model="date"
          type="date"
          value-format="yyyy年MM月dd日"
          placeholder="选择日期"
          :picker-options="pickerOptions">
        </el-date-picker>
        <el-button type="primary" size="mini" class="new-article-button" @click="newArticleClick">新建文章</el-button>
      </div>
      <!-- 新建文章模块     -->
      <Article v-if="newArticleViewVisuable" class="article"
               @deleteArticleSuccess="deleteArticleSuccess"
               @saveArticleSuccess="saveArticleSuccess"/>
      <!--  当天已创建文章列表模块    -->
      <Article class="article"
               v-for="(article,index) in articles"
               @deleteArticleSuccess="deleteArticleSuccess"
               @saveArticleSuccess="saveArticleSuccess"
               :key="index"
               :article="article"/>

    </div>
    <div class="home-right">
      <Summary/>
      <PunchinCalendar/>
    </div>
  </div>
</template>

<script>
import Article from "@/components/article/Article";
import Summary from "@/views/main/MainViewComps/BodyViews/Card";
import Todo from "@/components/Todo";
import PunchinCalendar from "@/components/PunchinCalendar";

import {formatDateToDateString} from "utils/DateUtil"
import {requestDeleteArticle, requestGetMoment} from "@/network/api";

export default {
  name: "HomeView",
  components: {
    Article,
    Summary,
    Todo,
    PunchinCalendar
  },
  created() {
    requestGetMoment().then(res => {
      if (res.data.code = "00000") {
        this.articles = [];
        for (let article of res.data.data) {
          //链接模块数据
          let links = []
          for (let url of article.urls) {
            links.push(new Object({'linkName': url.name, 'linkSrc': url.link}))
          }
          //标签模块数据
          let tags = []
          for (let tag of article.tags) {
            tags.push(tag.name)
          }
          //文章列表数据
          this.articles.push(new Object({
            'id': article.id,
            'title': article.title,
            'links': links,
            'sentiments': article.htmlContent,
            'tags': tags
          }))
        }
      }
    })
  },
  data() {
    return {
      newArticleViewVisuable: false,

      articles: [
        // {
        //   id: 2,
        //   title: '测试文章2',
        //   links: [
        //     {linkName: 'Github', linkSrc: 'https://www.github.com'},
        //     {linkName: '哔哩哔哩', linkSrc: 'https://www.bilibili.com'},
        //   ],
        //   sentiments: "# 标题\n" +
        //     "## 序言\n" +
        //     "段落\n" +
        //     "### Vue技术栈\n" +
        //     "1. mavonEditor\n" +
        //     "2. vuex\n" +
        //     "### 后端\n" +
        //     "* springboot\n" +
        //     "* redis",
        //   tags: ['TypeScript', 'SpringBoot'],
        // }
      ],

      date: formatDateToDateString(new Date()),
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date())
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }]
      }
    }
  },
  methods: {
    deleteArticleSuccess(id) {
      if (id === null) {
        this.newArticleViewVisuable = false
        return
      }

      //发送网络请求删除文章
      let articleFormData=new FormData()
      articleFormData.append("id",id)

      requestDeleteArticle(articleFormData).then(res => {
        if (res.data.code === "00000") {
          for (let i = 0; i < this.articles.length; i++) {
            if (this.articles[i].id === id) {
              this.articles.splice(i, 1)
            }
          }
          this.$message.success(res.data.message)
        }
      })

    },
    saveArticleSuccess(article) {
      let isNewArticle = true
      for (let i = 0; i < this.articles.length; i++) {
        if (this.articles[i].id === article.id) {
          this.articles.splice(i, 1, article)
          isNewArticle = false
        }
      }

      //若为新增文章
      if (isNewArticle) {
        this.articles.push(article)
        this.newArticleViewVisuable = false
      }
    },

    newArticleClick() {
      this.$notify.info({
        title: '提示',
        message: '请点击编辑按钮开始编辑',
        position: 'top-left'
      });
      this.newArticleViewVisuable = true;
    },
  }
}
</script>

