  float: left;
}

.date-picker {
  margin-left: 10px;
}

.new-article-button {
  margin-left: 100px
}

.date-wrapper {
  margin-bottom: 50px;
}

.article {
  margin-bottom: 50px;
}

.home-right {
  width: 30%;
